<section class="showing"><div class="showing__header"> <h2>{{  dataItem.item1.list }}</h2><button @click="sort()">{{ buttonText }}</button></div><div class="showing__content" v-if="isSort"> <showingItem v-for="(el,key,index) in dataItem" :key="index" :dataShow="el"></showingItem></div><div class="showing__unSort" v-if="!isSort">      <p v-for="item in sortItem(sorts)" v-html="item"> </p></div></section>