<section class="listMenu"> <div class="listMenu__header" @click="showTitle(title)"> <span class="listMenu__header-arrow" :class="{'listMenu__header-arrow-active':!isshowmenu}"></span><span class="listMenu__header-checkbox" :class="{'listMenu__header-checkbox-active':isshowmenu}"></span><h2 class="listMenu-title"> {{ title}}</h2></div><Transition name="itemList"><div class="listMenu__content" v-if="isshowmenu"> <ListItem :dataItemList="dataListElements"></ListItem></div></Transition></section>